import React, {memo, useState, useContext, useEffect} from 'react'
import {useForm, Form, Field, FormContext} from '@ttungbmt/react.form'
import {useFetch} from "@ttungbmt/react.http";
import Box from '@ttungbmt/react.ui.box';
import {find, findIndex, get, isEmpty} from 'lodash-es'
import {useDispatch} from '@ttungbmt/redux.toolkit'
import HorizontalTimeline from 'react-horizontal-timeline'

import StoreContext from "contexts/store"

function Badge({title}) {
    return <Box mb={1} className="badge badge-light badge-striped badge-striped-left border-left-primary"
                textSm>{title}</Box>
}

function FormMain() {
    const {dispatch: dsp, results} = useContext(StoreContext)
    const methods = useForm()

    const {data, loading} = useFetch({
        key: 'todos',
        url: '/api/dm/results',
        transformResponse: 'data',
        initialData: {}
    })

    const onXuly = (e) => {
        let value = parseInt(e.target.value),
            layersIdx = findIndex(data.results, {value})

        if (layersIdx >= 0) {
            dsp('setLayersIndex', layersIdx)
            let bounds = get(data, `results.${layersIdx}.bounds`)
            if(!isEmpty(bounds)) dsp('setBounds', bounds)
        } else {
            dsp('resetLayers')
        }
    }

    useEffect(() => {
        if (!loading) {
            dsp('setResults', data.results)
        }


    }, [loading])

    if (loading || !data) return null

    return (
        <Box p={1}>
            <FormContext {...methods}>
                <Form>
                    <div className="mt-2">
                        <Badge title={`CDI`}/>
                        <Box>
                            <Field
                                name="ma_kq"
                                as="select"
                                options={data.results || []}
                                placeholder="Chọn kết quả..."
                                onChange={onXuly}
                            />
                        </Box>
                    </div>
                </Form>
            </FormContext>
        </Box>


    )
}

export default memo(FormMain)
