import React, {memo} from 'react'
import {createContext} from 'react'
import {useImmerReducer} from 'use-immer'
import {includes, keys, isInteger} from 'lodash-es'

const initialState = {
    popup: null,
    layers: null,
    layersIndex: null,
    bounds: [],
    legends: [],
    results: [],
}

const caseReducer = {
    ['setLayers']: (state, {payload}) => {
        state.layers = payload
    },
    ['setLayersIndex']: (state, {payload}) => {
        state.layersIndex = payload
        if(isInteger(payload)){
            state.legends = state.results[payload].symbology
        }

    },
    ['resetLayers']: (state, {payload}) => {
        state.layers = null
        state.layersIndex = null
    },
    ['setResults']: (state, {payload}) => {
        state.results = payload
    },
    ['setBounds']: (state, {payload}) => {
        state.bounds = payload
    }
}

const reducer = (state, action) => includes(keys(caseReducer), action.type) ? caseReducer[action.type](state, action) : state

const StoreContext = createContext()

const StoreProvider = memo(({initialState, children}) => {
    const [state, dispatch] = useImmerReducer(reducer, initialState)

    const dispatchfn = (type, payload) => dispatch(({type, payload}))

    return (
        <StoreContext.Provider value={{...state, dispatch: dispatchfn}}>
            {children}
        </StoreContext.Provider>
    )
})


StoreProvider.defaultProps = {initialState}

export default StoreContext
export {StoreProvider}